const menuStyle = {
  height: '60px',
  color: 'white',
  'background-color': 'rgb(0, 174, 239)'
}

export default function Menu(props) {
  return (
    <div className="wrapper" style={menuStyle}>
      <div className="container menubar">
        <h1>Welcome to Kitabisa Test</h1>
      </div>
      <style jsx>{`
        .menubar {
          position: fixed;
          bottom: 0;
          align-items: center;
          padding: 0 14px;
        }
      `}</style>
    </div>
  )
}